import { useEffect, useState, useRef } from 'react'
import { useVariableInfo } from '../Contexts/VariablesContexts'
import './BingoType.css'

export default function Words(){
    const {
        sizeState, setSizeState,
        freeCellsState, setFreeCellsState,
        numberOfCardsState, setNumberOfCardsState,
        paperSizeState, setPaperSizeState,
        wordsState, setWordsState
    } = useVariableInfo()
    const [bodyData, setBodyData] = useState(<></>)
    const [newLineState, setNewLineState] = useState(false)
    const [updatedLineState, setUpdatedLineState] = useState(false)
    var elementRef = useRef(null)

    useEffect(()=>{
        setBodyData(wordsState.map((word, index)=>{
            return <tr key={"Bingo-Gen-Word-tr"+index}><td key={"Bingo-Gen-Word-td0"+index}>{index+1}</td><td key={"Bingo-Gen-Word-td1"+index} className='width-100perc'><input
                type="text"
                key={"Bingo-Gen-Word"+index}
                id={"Bingo-Gen-Word"+index}
                name={"word"+index}
                value={word}
                className='width-100perc'
                ref={ (index===wordsState.length-1)?elementRef:null }
                onChange={e=>{
                    var newArr = [...wordsState]
                    newArr[index] = e.target.value
                    setWordsState(newArr)
                }}
                onKeyDownCapture={e=>{
                    if(e.key==="Enter" && index===wordsState.length-1) {
                        setNewLineState(true)
                        setWordsState([...wordsState, ""])
                    }
                }}
            /></td></tr>
        }))

        if(newLineState) setUpdatedLineState(true)
    },[wordsState,setWordsState,newLineState])

    useEffect(()=>{
        if(elementRef.current != null && updatedLineState) {
            elementRef.current.focus()
            setUpdatedLineState(false)
        }
    },[bodyData,updatedLineState])

    return <div id="Bingo-Gen-Options">
    <div id="Bingo-Gen-Side-Words">
        <table id='Bingo-Gen-Words-Table'>
            <thead>
                <tr>
                    <td></td>
                    <td className='width-100perc'>Words</td>
                </tr>
            </thead>
            <tbody>
                {bodyData}
            </tbody>
        </table>
    </div>
    <div className="Bingo-Gen-Side">
        <div className="Bingo-Gen-Dropdown">
            <p>Size :</p>
            <select 
                id="Bingo-Gen-Size-Select" 
                name="Size"
                value={sizeState}
                onChange={e => setSizeState(isNaN(e.target.value)?"":parseInt(e.target.value))}
            >
                <option value={2}>2x2</option>
                <option value={3}>3x3</option>
                <option value={4}>4x4</option>
                <option value={5}>5x5</option>
                <option value={6}>6x6</option>
            </select>
        </div>
        <div className="Bingo-Gen-Dropdown">
            <p>Free Cells :</p>
            <select 
                id="Bingo-Gen-Free-Select" 
                name="Free Cells"
                value={freeCellsState}
                onChange={e => setFreeCellsState(isNaN(e.target.value)?"":parseInt(e.target.value))}
            >
                <option value={-1}>Center</option>
                <option value={0}>0</option>
                <option value={1}>1</option>
                <option value={2}>2</option>
                <option value={3}>3</option>
                <option value={4}>4</option>
                <option value={5}>5</option>
            </select>
        </div>
        <div className="Bingo-Gen-Dropdown">
            <p>Number of cards :</p>
            <input 
                type="number" 
                id="Bingo-Gen-Cards-Select" 
                name="Cards" 
                value={numberOfCardsState}
                onChange={e=>setNumberOfCardsState(isNaN(e.target.value)?"":parseInt(e.target.value))}
            />
        </div>
        <div className="Bingo-Gen-Dropdown">
            <p>Paper Size :</p>
            <select 
                id="Bingo-Gen-Size-Select" 
                name="Size"
                value={paperSizeState}
                onChange={e=>setPaperSizeState(isNaN(e.target.value)?"":parseInt(e.target.value))}
            >
                <option value={0}>A0</option>
                <option value={1}>A1</option>
                <option value={2}>A2</option>
                <option value={3}>A3</option>
                <option value={4}>A4</option>
                <option value={5}>A5</option>
            </select>
        </div>
    </div>
</div>
}