import React, {useEffect, useRef} from 'react'
import {HashRouter, Routes, Route} from 'react-router-dom'
import { VariablesContextProvider } from './Contexts/VariablesContexts';
import {DownloadNumbersButton, DownloadWordsButton} from './Downloadable/DownloadButton';
import colorData from './ColorScheme.json'
import NumberCard from './Downloadable/Cards/NumberCard';
import Numbers from './BingoTypes/Numbers';
import Words from './BingoTypes/Words'
import WordCard from './Downloadable/Cards/WordCard'
import './BingoShell.css'

export function NumberBingoShell(){  
    return <div id='Bingo-Gen-Shell'>
        <VariablesContextProvider>
            <div className="Bingo-Gen-Description">
                <p>Welcome to the Bingo Generator. You can set the Bingo card ratio, the number of free cells, the number of generated cards, and the size of the paper. Finally, you can download the Bingo game cards, call out sheets or both as PDF files.</p>
                <p>Both minimum and maximum numbers are inclusive.</p>
            </div>
            <div id='Bingo-Gen-Input-Shell'>
                <Numbers/>                    
                <NumberCard/>
                <DownloadNumbersButton/>
            </div>
        </VariablesContextProvider>
    </div>
}

export function WordBingoShell(){  
    return <div id='Bingo-Gen-Shell' >
        <VariablesContextProvider>
            <div className="Bingo-Gen-Description">
                <p>Welcome to the Bingo Generator. You can set the Bingo card ratio, the number of free cells, the number of generated cards, and the size of the paper. Finally, you can download the Bingo game cards, call out sheets or both as PDF files.</p>
                <p>To enter more words, just press "Enter" in the bottom most word input field.</p>
            </div>
            <div id='Bingo-Gen-Input-Shell'>
                <Words/>
                <WordCard/>
                <DownloadWordsButton/>
            </div>
        </VariablesContextProvider>
    </div>
}

export function BingoShell(){
    const surroundingRef = useRef()
  
    useEffect(()=>{
      if(surroundingRef==null)return
      if(surroundingRef.current==null)return
      let style = surroundingRef.current.style
      style.setProperty('--backgroundColor',colorData.backgroundColor)
      style.setProperty('--hoverColor',colorData.hoverColor)
      style.setProperty('--textColor',colorData.textColor)
      style.setProperty('--primaryColor',colorData.primaryColor)
      style.setProperty('--secondaryColor',colorData.secondaryColor)
    },[])

    console.log(window.location.pathname);

    var isWord = window.location.pathname==="/BingoGenerator/wordbingo"

    return <div ref={surroundingRef}>
    <HashRouter>
        <Routes>
            <Route path={process.env.PUBLIC_URL+'/'} element={<NumberBingoShell/>}/>
            <Route path={process.env.PUBLIC_URL+'/wordBingo'} element={<WordBingoShell/>}/>
        </Routes>
    </HashRouter>
    </div>
}