import { useVariableInfo } from "../Contexts/VariablesContexts"
import './DownloadButton.css'

export function DownloadNumbersButton(){
    const {
        canDownloadNumbers
    } = useVariableInfo()

    return <div id="Bingo-Gen-Download">
        <div 
            id="Bingo-Gen-Download-Button"
            onClick={()=>{canDownloadNumbers(0)}}
        >Download Bingo Cards and Call Out Sheet PDF</div>
        <div 
            id="Bingo-Gen-Download-Button"
            onClick={()=>{canDownloadNumbers(1)}}
        >Download Bingo Cards PDF</div>
        <div 
            id="Bingo-Gen-Download-Button"
            onClick={()=>{canDownloadNumbers(2)}}
        >Download Call Out Sheet PDF</div>
    </div>
}

