import { useEffect, useState } from 'react'
import { useVariableInfo } from '../../Contexts/VariablesContexts'
import './Card.css'

const freeCellString = "Bingo-Gen-Free-Cell-String"
const blankString = "Bingo-Gen-Blank-String"

export default function WordCard(){
    const [bingoState, setBingoState] = useState()
    const {wordsState, numbersNeededState, freeCellsState, sizeState, getWordsArray} = useVariableInfo()

    useEffect(()=>{
        var arr = getWordsArray(wordsState, numbersNeededState, freeCellsState, sizeState)

        setBingoState(<table><tbody>{
            [...Array(sizeState)].map((e0,i0)=><tr key={"Bingo-Gen-NumberCard-tr"+i0}>
                {[...Array(sizeState)].map((e1, i1)=>{
                    let cell = arr[(i0*sizeState)+i1]
                    
                    if(cell===freeCellString){
                        return <td 
                            key={"Bingo-Gen-NumberCard-td"+(i0*sizeState)+i1}
                            className="Bingo-Gen-Card-Cell"
                        ><div className="Bingo-Gen-Free-Cell" /></td>
                    }else if(cell===blankString){
                        return <td 
                            key={"Bingo-Gen-NumberCard-td"+(i0*sizeState)+i1}
                            className="Bingo-Gen-Card-Cell"
                        > </td>
                    }else{
                        return <td key={"Bingo-Gen-NumberCard-td"+(i0*sizeState)+i1} className="Bingo-Gen-Card-Cell">
                            {cell}
                        </td>
                    }
                })}
            </tr>)
        }</tbody></table>)
    },[wordsState, numbersNeededState, freeCellsState, sizeState, getWordsArray])
    
    return <div className="Bingo-Gen-Card-Shell">
        <p>BINGO</p>
        {bingoState}
    </div>
}