import jsPDF from "jspdf";
import { freeCellString, blankString } from "./Contexts/VariablesContexts";    
import {renderToString} from 'react-dom/server'
import './Fonts/GloriaHallelujah-Regular-normal'

export const SavePDF=(arrs, gameSize, paperSize, download)=>{
    var doc = new jsPDF('portrait', 'px', pageNames[paperSize])
    doc.setFont('GloriaHallelujah-Regular')

    const addBingoGame=(v, thisDoc)=>{
        var arr = arrs[0][v]

        let htmlElement = <div style={{
            width:doc.internal.pageSize.getWidth()+"px",
            height:doc.internal.pageSize.getHeight()+"px",
            display:'flex',
            alignContent: 'center',
            justifyContent: 'center',
            fontFamily:'GloriaHallelujah-Regular',
            alignItems: 'center'
        }}><div style={{
            backgroundColor: "grey",
            borderRadius:'1%',
            margin: 'auto',
            padding: '1vw',
            width: (doc.internal.pageSize.getWidth()*.8)+"px",
            height: (doc.internal.pageSize.getHeight()*.8)+"px",
            display:'flex',
            alignContent: 'center',
            justifyContent: 'center',
            alignItems: 'center',
            flexDirection:'column'
        }}>
            <p style={{
                margin: '0',
                color:"white",
                fontSize: '15vh'
            }}>BINGO</p>
            <div
                style={{
                    width:'100%',
                    display:'flex',
                    flexDirection:'column',
                    flex:'1 1 0'
                }}
            >
                {[...Array(gameSize)].map((e0,i0)=><div 
                    key={"Bingo-Gen-NumberCard-tr"+i0}
                    style={{
                        display:"flex",
                        flex:'1 1 0'
                    }}
                >
                    {[...Array(gameSize)].map((e1, i1)=>{
                        let cell = arr[(i0*gameSize)+i1]
                        const cellTextLength = (""+cell).length
                        var fontNumber = (gameSize<3?10:2.5)/cellTextLength * (6-paperSize)

                        var fontFamily

                        thisDoc.setFont('GloriaHallelujah-Regular')
                        fontFamily='GloriaHallelujah-Regular'

                        var fontSize = fontNumber+'vw'

                        return<div
                            key={"Bingo-Gen-NumberCard-td"+(i0*gameSize)+i1}
                            style={{
                                backgroundColor: "white",
                                display:"flex",
                                borderRadius:'10%',
                                margin:'1%',
                                flex:'1 1 0'
                            }}  
                        >
                            {(cell===freeCellString)?<div style={{
                                margin:'auto',
                                height:'70%',
                                width:'70%',
                                backgroundColor: "grey",
                                borderRadius: '100%'
                            }} />:(cell===blankString)?"":<p
                                style={{
                                    fontFamily:fontFamily,
                                    margin:'auto',
                                    color:'grey',
                                    fontSize:fontSize//clamp(0.1vw, 5vw/'+cellTextLength+', 5vw)'
                                }}
                                >{cell}</p>
                            }
                        </div>
                    })}
                </div>)}
            </div>
        </div></div>

        let elementAsString = renderToString(htmlElement);
        thisDoc.html(elementAsString, {
            callback: function (newDoc) {
                if(v+1 < arrs[0].length){
                    newDoc.insertPage(1)
                    addBingoGame(v+1, newDoc)
                }else{
                    if(download===1) newDoc.deletePage(doc.internal.getNumberOfPages())
                    newDoc.save("Bingo.pdf");
                }
            }
        });
    }

    const addCallOut=(addPage)=>{
        const width = doc.internal.pageSize.getWidth(),
        height = doc.internal.pageSize.getHeight(),
        yPos = (height*.05),
        bingoWidth = width*.8,
        bingoHeight = height*.9,
        cellMultiplier = .9/gameSize,
        cellSpacingMultiplier = .1/(gameSize+2),
        cellsWidth = bingoWidth*cellMultiplier, 
        cellsHeight = bingoHeight-(bingoHeight*(.1+cellSpacingMultiplier)),
        cellYSpacing = cellsHeight*cellMultiplier,
        xSpacing = bingoWidth*cellSpacingMultiplier,
        radius = ((cellsWidth-xSpacing>cellYSpacing)?cellYSpacing:(cellsWidth-xSpacing))*.45,
        fontSize = radius,
        headerSize = radius*1.5

        const callOutDims = [(width*.9), (height*.85)]
        const callOutOffset = [(width*.05), (height*.05)]

        const maxWidth = (callOutDims[0] - (callOutOffset[0]*2))
        const thirdWidth = maxWidth/3
        const columnOffset = [callOutOffset[0]*2, (callOutOffset[0]*2+thirdWidth), (callOutOffset[0]*2+(thirdWidth*2))]

        const rowCount = (callOutDims[1]/(callOutOffset[1]))-1

        var index = 0

        const setupNewPage=(addPage)=>{
            if(addPage) doc.addPage()
            doc.setTextColor(89, 206, 143)
            var text = "Call Out Sheet"
            doc.setFontSize(headerSize)
            var xOffset = (doc.internal.pageSize.width / 2) - doc.getTextDimensions(text).w/2; 
            doc.text(text, xOffset, yPos+doc.getTextDimensions(text).h/2);
    
            //reset font size
            doc.setFontSize(fontSize)
    
            //Prep to draw outline
            doc.setLineWidth(5)
            doc.setDrawColor(89, 206, 143)

            //draw outline
            doc.roundedRect(callOutOffset[0], callOutOffset[1]*2, callOutDims[0], callOutDims[1], 4, 4, 'S')
        }

        setupNewPage(addPage)

        while(index < arrs[1].length){
            for(let i = 0; i < rowCount; i++){
                const yPos = callOutOffset[1]*(3+i)
                for(let j = 0; j < 3; j++){
                    //draw square
                    doc.roundedRect(columnOffset[j], yPos-(fontSize/2), fontSize/2, fontSize/2, 0.5, 0.5, 'S')        
                    //add text
                    doc.text(arrs[1][index], columnOffset[j]+(fontSize*.75), yPos, {maxWidth:columnOffset[0]-15})
    
                    if(++index >= arrs[1].length) break;
                }
                if(index >= arrs[1].length) break;
            }
            if(index >= arrs[1].length) break;
            
            setupNewPage(true)
        }
    }

    switch(download){
        case 0://both
            addCallOut(true)
            addBingoGame(0, doc)
            break;
        case 1://cards
            addBingoGame(0, doc)
            break;
        case 2://call out
            addCallOut(false)
            doc.save("Bingo.pdf");
            break;
        default: break;
    }
}

const pageNames = [
    'a0','a1','a2','a3','a4','a5','a6','a7','a8','a9', 'a10'
]

