import { useContext, createContext, useState, useEffect, useRef } from "react";
import { SavePDF } from "../SavePDF";

const VariablesContext = createContext()

export const freeCellString = "Bingo-Gen-Free-Cell-String"
export const blankString = "Bingo-Gen-Blank-String"

export const useVariableInfo=()=>{return useContext(VariablesContext)}

export const VariablesContextProvider = ({children})=>{
    const [tabState, setTabState] = useState(false)
    const [sizeState, setSizeState] = useState(3)
    const [freeCellsState, setFreeCellsState] = useState(-1)
    const [numberOfCardsState, setNumberOfCardsState] = useState(6)
    const [paperSizeState, setPaperSizeState] = useState(4)
    const [minimumNumberState, setMinimumNumberState] = useState(1)
    const [maximumNumberState, setMaximumNumberState] = useState(5)
    const [wordsState, setWordsState] = useState([...Array((sizeState*sizeState) - Math.abs(freeCellsState))].map(()=>""))
    const [numbersNeededState, setNumbersNeededState] = useState((sizeState*sizeState) - Math.abs(freeCellsState))

    const snackRef = useRef(null)

    const canDownloadNumbers =(downloadState)=>{  
        //check min/max number
        if((maximumNumberState-minimumNumberState+1) >= numbersNeededState){              
            var arrays = []
            const arr = [...Array(maximumNumberState-minimumNumberState+1)].map((i, e)=>""+(e+minimumNumberState)) 
            shuffle(arr)

            for(let i = 0; i < numberOfCardsState; i++){
                arrays.push([...getNumberArray(maximumNumberState, minimumNumberState, numbersNeededState, freeCellsState, sizeState)])
            }

            SavePDF([arrays, arr], sizeState, paperSizeState, downloadState)
        }else{
            showError("There are not enough numbers to create a Bingo card.\nDecrease the minimum or increase the maximum to download a PDF.")
        }
    }

    const canDownloadWords =(downloadState)=>{        
        const filteredWords = wordsState.filter((e)=>e!=='')       
        //check words
        if(filteredWords.length >= numbersNeededState){
            var arrays = []
            const arr = [...filteredWords]
            shuffle(arr)
            
            for(let i = 0; i < numberOfCardsState; i++){
                arrays.push([...getWordsArray(filteredWords, numbersNeededState, freeCellsState, sizeState)])
            }

            SavePDF([arrays, arr], sizeState, paperSizeState, downloadState)
        }else{
            showError("There are not enough words to create a Bingo card.\nAdd more words to download a PDF.")
        }
    }

    const showError = (message)=>{
        snackRef.current.innerHTML = "<p>"+message+"</p>"
        snackRef.current.className = "show"
        setTimeout(()=>{ snackRef.current.className = snackRef.current.className.replace("show", ""); }, 2900);
    }

    const getWordsArray = (words, numbsNeeded, freeSpaces, sizeState)=>{
        var arr = [...(words.filter((e)=>e!==''))]
    
        var mid = ((sizeState*sizeState)/2)
        mid += mid%2===0?2:0;
    
        if(arr.length<numbsNeeded){
            while(arr.length<numbsNeeded) arr.push(blankString)
            shuffle(arr)    
        }else{
            shuffle(arr)    
            arr.splice(numbsNeeded)    
        }
    
        if(freeSpaces<0){
            arr.splice(mid, 0, freeCellString)
        }else{
            for(let i = 0; i < freeSpaces;i++){
                arr.push(freeCellString)
            }
        
            shuffle(arr)
        }
    
        return arr
    }

    const getNumberArray = (max, min, numbsNeeded, freeSpaces, sizeState)=>{
        if(max < min) return null

        var arr = [...Array(max-min+1)].map((i, e)=>""+(e+min))
    
        var mid = ((sizeState*sizeState)/2)
        mid += mid%2===0?2:0;
    
        if(arr.length<numbsNeeded){
            while(arr.length<numbsNeeded) arr.push(blankString)
            shuffle(arr)    
        }else{
            shuffle(arr)    
            arr.splice(numbsNeeded)    
        }
    
        if(freeSpaces<0){
            arr.splice(mid, 0, freeCellString)
        }else{
            for(let i = 0; i < freeSpaces;i++){
                arr.push(freeCellString)
            }
        
            shuffle(arr)
        }
    
        return arr
    }

    const shuffle = (array) => {
        for (var i = array.length - 1; i > 0; i--) {
          var j = Math.floor(Math.random() * (i + 1));
          var temp = array[i];
          array[i] = array[j];
          array[j] = temp;
        }
    }

    useEffect(()=>{
        setNumbersNeededState((sizeState*sizeState) - Math.abs(freeCellsState))
        var minMaxState = maximumNumberState-minimumNumberState+1

        if(minMaxState<numbersNeededState){
            setMinimumNumberState(1)
            setMaximumNumberState(numbersNeededState)
        }

        const wordsNeeded = (sizeState*sizeState) - Math.abs(freeCellsState) - wordsState.length
        
        if(wordsNeeded > 0) setWordsState([...wordsState, ...[...Array(wordsNeeded)].map(()=>"")])
    },[sizeState,maximumNumberState,minimumNumberState,freeCellsState,numbersNeededState,wordsState])

    return <VariablesContext.Provider value={{
        tabState, setTabState,
        sizeState, setSizeState,
        freeCellsState, setFreeCellsState,
        numberOfCardsState, setNumberOfCardsState,
        paperSizeState, setPaperSizeState,
        minimumNumberState, setMinimumNumberState,
        maximumNumberState, setMaximumNumberState,
        wordsState, setWordsState,
        canDownloadNumbers, canDownloadWords,
        numbersNeededState, 
        getNumberArray, getWordsArray,
        shuffle
    }}>
        <div id="Bingo-Gen-snackbar" ref={snackRef} />
        {children}
    </VariablesContext.Provider>
}