import { useEffect, useState } from 'react'
import { useVariableInfo, freeCellString, blankString } from '../../Contexts/VariablesContexts'
import './Card.css'

export default function NumberCard(){
    const [bingoState, setBingoState] = useState()
    const {maximumNumberState, minimumNumberState, numbersNeededState, freeCellsState, sizeState, getNumberArray} = useVariableInfo()

    useEffect(()=>{
        if(!(isNaN(maximumNumberState) || 
            isNaN(minimumNumberState) ||
            isNaN(numbersNeededState) ||
            isNaN(freeCellsState) ||
            isNaN(sizeState)
        )){
            var arr = getNumberArray(maximumNumberState, minimumNumberState, numbersNeededState, freeCellsState, sizeState)
            if(arr !== null){
                setBingoState(<table
                    style={{
                        fontFamily:'KgTraditionalFractions2-Pl5m'
                    }}
                ><tbody>{
                    [...Array(sizeState)].map((e0,i0)=><tr key={"Bingo-Gen-NumberCard-tr"+i0}>
                        {[...Array(sizeState)].map((e1, i1)=>{
                            let cell = arr[(i0*sizeState)+i1]
        
                            if(cell===freeCellString){
                                return <td 
                                    key={"Bingo-Gen-NumberCard-td"+(i0*sizeState)+i1}
                                    className="Bingo-Gen-Card-Cell"
                                ><div className="Bingo-Gen-Free-Cell" /></td>
                            }else if(cell===blankString){
                                return <td 
                                    key={"Bingo-Gen-NumberCard-td"+(i0*sizeState)+i1}
                                    className="Bingo-Gen-Card-Cell"
                                > </td>
                            }else{
                                return <td key={"Bingo-Gen-NumberCard-td"+(i0*sizeState)+i1} className="Bingo-Gen-Card-Cell">
                                    {cell}
                                </td>
                            }
                        })}
                    </tr>)
                }</tbody></table>)
            }
        }
    },[maximumNumberState, minimumNumberState, numbersNeededState, freeCellsState, sizeState, getNumberArray])
    
    return <div className="Bingo-Gen-Card-Shell">
        <p>BINGO</p>
        {bingoState}
    </div>
}